  padding: 33px 0;
  line-height: 17px; }

.header__logo-wrapper {
  flex-grow: 1; }

.header__link {
  position: relative;
  padding-left: 37px;
  margin-right: 48px;
  margin-left: 39px;
  font-family: "PT Mono", "Arial", sans-serif;
