.footer__column:nth-child(2) .footer__item {
  width: 47%; }

.footer__list--flex .footer__item:nth-child(2) {
  order: 3; }

.footer__list--flex .footer__item:nth-child(3) {
  order: 5; }

.footer__list--flex .footer__item:nth-child(4) {
  order: 7; }

.footer__list--flex .footer__item:nth-child(5) {
  order: 2; }

.footer__list--flex .footer__item:nth-child(6) {
  order: 4; }

.footer__list--flex .footer__item:nth-child(7) {
  order: 6; }

.footer__link {
