  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 15px;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  margin-bottom: 29px;
  letter-spacing: 0.05em; }

.footer__line {
  width: 100%;
  height: 1px;
  margin-top: 58px;
  background-color: #626962;
  opacity: 0.2; }

.footer__copyright {
  font-size: 13px;
  line-height: 24px;
  font-weight: normal;
  display: flex;
  justify-content: space-between;
  padding: 14px 0;
  opacity: 0.5; }

.footer__copyright-item {
  text-transform: uppercase;
  letter-spacing: 0.04em;
