  .feedback__title {
    margin: 0;
    padding: 0;
    margin-bottom: 10px;
    font-size: 36px;
    line-height: 51px;
    font-weight: normal; }
  .feedback__text {
    margin: 0;
    padding: 0;
    padding-left: 5px;
    font-size: 16px;
    line-height: 22px;
    font-weight: normal;
    font-family: "PT Mono", "Arial", sans-serif; }
  .feedback__form {
    display: flex;
